@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@600&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
}

html {
  min-height: 100%;
}

body{
  min-height:100%;
  height:100vh;
}


h1{
  text-align:center;
  font-size: 4em;
  font-family: 'Playfair Display', serif; 
  padding-top: 40px;
  padding-bottom: 20px;
}

